import React, { useState } from "react";
import { Form, Stack } from "react-bootstrap";
import CustomButton from "../../components/Button/Button";
import FileUpload from "../../components/FileUploader/FileUploader";
import CustomModal from "../../components/Modal/customModal";
import { downloadTemplate } from "../../utils/helpers";
import useToast from "../../hooks/useToast";

const MassUpload = () => {
  const [modalShow, setModalShow] = useState(false);
  const [errorModalShow, setErrorModalShow] = useState(false);
  const [validationErrors, setValidationErrors] = useState([]);
  const [operationChoice, setOperationChoice] = useState("");
  const { showErrorToast } = useToast();

  const handleFileUpload = (data) => {
    console.log("Validated Excel Data:", data);
    // Proceed with further processing of valid data...
  };

  // Handle the download template action
  const handleDownloadTemplate = () => {
    downloadTemplate(showErrorToast)(operationChoice);
  };

  // When FileUpload finds validation errors, this callback is invoked.
  const handleValidationError = (errors) => {
    setValidationErrors(errors);
    setErrorModalShow(true);
  };

  return (
    <>
      <Stack className="mt-3" gap={4}>
        {/* Choose operation and Download Template */}
        <Stack direction="horizontal">
          <Form.Select
            aria-label="Choose Operations"
            style={{
              width: "30%",
              border: "1px solid #007bff",
              outline: "none",
              borderRadius: "8px",
            }}
            onChange={(e) => setOperationChoice(e.target.value)}
          >
            <option value="">Choose Operations</option>
            <option value="1">Physical Product/Raw Material</option>
            <option value="2">Physical Product Structure</option>
            <option value="3">Document</option>
            <option value="4">Physical Product-Document</option>
          </Form.Select>

          <CustomButton
            variant="link ms-auto btn-lg"
            onClick={handleDownloadTemplate}
            text="Download Template"
          />
        </Stack>

        {/* Drag and Drop container */}
        <FileUpload
          fileTypes={["XLSX"]}
          multiple={false}
          onUpload={handleFileUpload}
          onValidationError={handleValidationError}
          operationChoice={operationChoice}
        />

        {/* Form-CTA submit */}
        <Stack direction="horizontal">
          <Form.Check type="checkbox" label="background" />
          <CustomButton
            variant="primary ms-auto"
            onClick={() => setModalShow(true)}
            text="Submit"
          />
        </Stack>
      </Stack>

      {/* Reusable Modal for Submit */}
      <CustomModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        title="Custom Modal Title"
        footerButtons={[
          {
            label: "Cancel",
            variant: "danger",
            onClick: () => setModalShow(false),
          },
          {
            label: "Confirm",
            variant: "success",
            onClick: () => alert("Confirmed!"),
          },
        ]}
      >
        <p>This is a reusable modal component with dynamic content.</p>
      </CustomModal>

      {/* Error Modal; displays validation error messages */}
      <CustomModal
        show={errorModalShow}
        onHide={() => setErrorModalShow(false)}
        title="Validation Errors"
        footerButtons={[
          {
            label: "Close",
            variant: "primary",
            onClick: () => setErrorModalShow(false),
          },
        ]}
      >
        <ul>
          {validationErrors.map((error, idx) => (
            <li key={idx}>{error}</li>
          ))}
        </ul>
      </CustomModal>
    </>
  );
};

export default MassUpload;
